$border-radius: 3px;
$box-shadow: 0px 2px 2px rgba(0,0,0,0.25);
$box-shadow-hover: 2px 4px 4px rgba(0,0,0,0.35);
$blue: red;
.bx--container {
    background: #f2f2f2;
    border: 1px solid #ccc;
    position: relative;
}

.bx--button {
    background: rgba(0,0,0,0.15);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 2rem;
    text-transform: uppercase;
    border: 1px solid rgba(0,0,0,0.25);
    border-radius: $border-radius;
    box-shadow: $box-shadow;
    transition: all 150ms;
    &.active {
        background: rgba(0,0,0,0.35);
        color: white;
        cursor: default;
    }
    &:hover {
        cursor: pointer;
        background: rgba(0,0,0,0.10);
        box-shadow: $box-shadow-hover;
    }
}

.bx--button-wrapper {
    display: flex;
    justify-content: center;
    list-style-type: none;
    margin: 1.5rem 0 .5rem 0;
    padding: 0;
    > * {
        margin: 0 .75rem;
    }
}

.max-width {
    width: 80%;
    margin: 0 auto;
}

.center-content {
    text-align: center;
}

.add-padding {
    padding: 1rem 2rem;
}







































































































































#book-wrapper {
    position: relative;
    width: 60%;
    margin: 3rem auto;
    border: 2px solid #ccc;
    padding: 4rem;
    box-shadow: 10px 10px 0px 0px rgba(0,0,0,0.1);
    max-height: 600px;
    overflow-y: scroll;
}
.agenda-entry {
    text-align: left;
    vertical-align: top;
    border: none;

    td {
        border-top: none !important;
    }
}
.abstracts-wrapper {
    text-align: justify;
}
.section {
    font-family: inherit;
    font-size: 10pt;
}
.section-title {
    font-weight: bold;
}
.abstract__title {
    font-weight: bold;
}
.abstract__authors:after {
    content: '. ';
}
#no-book-data {
    border: 1px solid rgba(0,0,0,0.1);
    background: #f2f2f2;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 300px;
    max-width: 700px;
    margin: 50px auto;
    .loading & {
        opacity: 0.35;
    }
    .message {
        font-size: 1.5rem;
        font-weight: bold;
    }
}
.bx--button svg {
    height: 45px;
    width: auto;
    position: relative;
    left: -0.5rem;
    fill: #295699;
}
.no-orals-message {
    display: flex;
    margin: 0 auto;
    background: #f2f2f2;
    border: 1px solid #ccc;
    width: 80%;
    height: 250px;
    justify-content: center;
    align-items: center;
    display: none;
    &:first-child {
        display: flex;
    }
}
.no-presentation {
    opacity: 0.5;
}
.agenda-oral--presentation-link {
    font-size: 1.25em;
    padding-top: 0.5em;
}
